import { memo } from 'react';

import { formLabels } from './config';
import {
  Form,
  FormField,
  Input,
  Label,
  SubmitButton,
  Textarea,
} from './styled';
import { ContactsFormProps } from './types';

const ContactsForm = (props: ContactsFormProps) => {
  const { formValues, onInputChange, onSubmit } = props;

  return (
    <Form onSubmit={onSubmit}>
      <FormField>
        <Label htmlFor="name">{formLabels.name}</Label>
        <Input
          type="text"
          id="name"
          name="name"
          value={formValues.name}
          onChange={onInputChange}
          required
        />
      </FormField>

      <FormField>
        <Label htmlFor="email">{formLabels.email}</Label>
        <Input
          type="email"
          id="email"
          name="email"
          value={formValues.email}
          onChange={onInputChange}
          required
        />
      </FormField>

      <FormField>
        <Label htmlFor="message">{formLabels.message}</Label>
        <Textarea
          id="message"
          name="message"
          value={formValues.message}
          onChange={onInputChange}
          required
        />
      </FormField>

      <SubmitButton type="submit">{formLabels.buttonText}</SubmitButton>
    </Form>
  );
};

export const MemoizedContactsForm = memo(ContactsForm);
