import { styled } from 'styled-components';

export const Container = styled.div`
  display: flex;
  flex-direction: column;
`;

export const Title = styled.h2`
  font-size: ${({ theme }) => theme.font.fontSizes.l};
  font-weight: ${({ theme }) => theme.font.fontWeights.bold};
  color: ${({ theme }) => theme.colors.primaryText};

  margin-bottom: ${({ theme }) => theme.spacing.m};

  @media ${({ theme }) => theme.mediaQuery.mobile} {
    text-align: center;
    font-size: ${({ theme }) => theme.font.fontSizes.m};
  }
`;

export const ContactInfo = styled.div`
  margin-top: ${({ theme }) => theme.spacing.l};
`;

export const Subtitle = styled.h3`
  font-size: ${({ theme }) => theme.font.fontSizes.m};
  font-weight: ${({ theme }) => theme.font.fontWeights.normal};
  color: ${({ theme }) => theme.colors.cardText};

  @media ${({ theme }) => theme.mediaQuery.tablet} {
    font-size: ${({ theme }) => theme.font.fontSizes.s};
  }

  @media ${({ theme }) => theme.mediaQuery.mobile} {
    font-size: ${({ theme }) => theme.font.fontSizes.xs};
  }
`;

export const Text = styled.p`
  font-size: ${({ theme }) => theme.font.fontSizes.s};
  font-weight: ${({ theme }) => theme.font.fontWeights.normal};
  color: ${({ theme }) => theme.colors.primaryText};

  @media ${({ theme }) => theme.mediaQuery.mobile} {
    font-size: ${({ theme }) => theme.font.fontSizes.xs};
  }
`;

export const ContactsContainer = styled.div`
  margin: ${({ theme }) => theme.spacing['3xl']} 0;
`;
