import { styled } from 'styled-components';

export const Form = styled.form`
  display: flex;
  flex-direction: column;

  background: ${({ theme }) => theme.colors.cardBackground};
  border-radius: 10px;
  padding: ${({ theme }) => theme.spacing.m};

  max-width: ${({ theme }) => theme.size.elementSize.l};
  width: 100%;
`;

export const FormField = styled.div`
  margin-bottom: ${({ theme }) => theme.spacing.m};
`;

export const Label = styled.label`
  display: block;
  font-weight: ${({ theme }) => theme.font.fontWeights.bold};
  font-size: ${({ theme }) => theme.font.fontSizes.m};
  color: ${({ theme }) => theme.colors.cardText};

  margin-bottom: ${({ theme }) => theme.spacing.s};

  @media ${({ theme }) => theme.mediaQuery.tablet} {
    font-size: ${({ theme }) => theme.font.fontSizes.s};
  }

  @media ${({ theme }) => theme.mediaQuery.mobile} {
    font-size: ${({ theme }) => theme.font.fontSizes.xs};
  }
`;

const InputBase = styled.input`
  width: 100%;
  padding: ${({ theme }) => theme.spacing.s};

  border: 2px solid transparent;
  border-radius: 5px;

  font-size: ${({ theme }) => theme.font.fontSizes.s};

  background: ${({ theme }) => theme.colors.background};
  color: ${({ theme }) => theme.colors.primaryText};

  &:focus {
    border: 2px solid ${({ theme }) => theme.colors.activeLink};
  }
`;

export const Input = styled(InputBase).attrs({ as: 'input' })``;

export const Textarea = styled(InputBase).attrs({ as: 'textarea' })`
  resize: vertical;
`;

export const SubmitButton = styled.button`
  width: 100%;
  padding: ${({ theme }) => theme.spacing.s};

  border-radius: 5px;
  border: 2px solid ${({ theme }) => theme.colors.cardBorder};

  font-size: ${({ theme }) => theme.font.fontSizes.s};

  background: ${({ theme }) => theme.colors.background};
  color: ${({ theme }) => theme.colors.primaryText};

  cursor: pointer;

  max-width: ${({ theme }) => theme.size.elementSize.s};
  width: 100%;

  align-self: flex-end;

  &:hover {
    background: ${({ theme }) => theme.colors.cardBackground};
  }

  @media ${({ theme }) => theme.mediaQuery.tablet} {
    max-width: ${({ theme }) => theme.size.elementSize.l};
  }
`;
