import { ChangeEvent, FormEvent, useCallback, useState } from 'react';
import { Toast } from '@components/Modals/Toast';
import { MemoizedContactsForm as ContactsForm } from '@components/UI/Form/ContactsForm';

import { contactInfo, successMessage } from './config';
import {
  ContactInfo,
  ContactsContainer,
  Container,
  Subtitle,
  Text,
  Title,
} from './styled';
import { ContactFormValues } from './types';

export const ContactsSection = () => {
  const [formValues, setFormValues] = useState<ContactFormValues>({
    name: '',
    email: '',
    message: '',
  });

  const [isSubmitted, setIsSubmitted] = useState<boolean>(false);
  const [showToast, setShowToast] = useState<boolean>(false);

  const handleInputChange = useCallback(
    (event: ChangeEvent<HTMLInputElement | HTMLTextAreaElement>) => {
      const { name, value } = event.target;
      setFormValues((prevValues) => ({ ...prevValues, [name]: value }));
    },
    [],
  );

  const handleSubmit = useCallback((event: FormEvent<HTMLFormElement>) => {
    event.preventDefault();
    setIsSubmitted(true);
    setShowToast(true);
  }, []);

  const handleToastClose = () => {
    setShowToast(false);
  };

  return (
    <Container>
      <ContactsContainer>
        <Title>{contactInfo.contactsTitle}</Title>
        <ContactInfo>
          <Subtitle>{contactInfo.addressTitle}</Subtitle>
          <Text>{contactInfo.address}</Text>
        </ContactInfo>
        <ContactInfo>
          <Subtitle>{contactInfo.phoneTitle}</Subtitle>
          <Text>{contactInfo.phone}</Text>
        </ContactInfo>
        <ContactInfo>
          <Subtitle>{contactInfo.emailTitle}</Subtitle>
          <Text>{contactInfo.email}</Text>
        </ContactInfo>
      </ContactsContainer>

      <Title>{contactInfo.formTitle}</Title>
      <ContactsForm
        formValues={formValues}
        onInputChange={handleInputChange}
        onSubmit={handleSubmit}
      />

      {showToast && isSubmitted && (
        <Toast message={successMessage} onClose={handleToastClose} />
      )}
    </Container>
  );
};
